<template>
  <div class="studentFile">
    
    <div class="tableTop">
      <div class="tableTitle"><crumb></crumb></div>
      
      <el-select v-model="model" placeholder="文件类型">
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
    </div>
    <div class="table">
      <el-table
        :data="tableData"
        style="width: 100%">
         <el-table-column type="expand">
          <template slot-scope="props">
            <div style="overflow:hidden;">
              <span style="float:left;font-weight:bold;font-size:14px;line-height: 30px">其他附件：</span>
              <div style="float:left;overflow:hidden;padding-left: 20px;" v-for="(item, index) in props.row.other" :key="index">
                <div style="float:left; margin: 0 50px 20px 0;overflow: hidden">
                  <div style="border:1px solid rgba(221,221,221,1);border-radius:5px;padding: 5px 10px; float:left;background:#eee">{{item.fileName}}</div>
                  <svgIcon icon-class="iconxiazai" style="margin-left:10px;float:left;font-size: 20px;margin-top: 3px;cursor: pointer"></svgIcon>
                </div>
              </div>
            </div>
          </template>
        </el-table-column>
        <el-table-column
          v-for="(item, index) in tableTitle"
          :key="index"
          :prop="item.prop"
          :label="item.label"
          :width="item.width">
        </el-table-column>
        <el-table-column
          label="操作">
          <template slot-scope="scope">
            <div style="float: left; margin-right: 30px">
              <SvgIcon icon-class="iconyulan"></SvgIcon>
              <el-button type="text" size="small" @click="view">预览</el-button>
            </div>
            <div style="float: left">
              <SvgIcon icon-class="iconxiazaizhengshu"></SvgIcon>
              <el-button type="text" size="small" >下载文件</el-button>
            </div>
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>
<script>
export default {
  name: 'student',
  data() {
    return {
      model: '',
      options: [],
      tableTitle: [
        {
          label: '文件名称',
          prop: 'fileTitle'
        },
        {
          label: '发布时间',
          prop: 'time'
        },
        {
          label: '下载次数',
          prop: 'count'
        }
      ],
      tableData: [
        {
          fileTitle: '关于印发《本科招生工作管理办法》的通知',
          time: '2016-2017学年  第一学期',
          count: '56次',
          other: [
            {
              fileName: '优质生源基地协议书及反馈表'
            },
            {
              fileName: '学校招生宣传渠道和平台'
            },
            {
              fileName: '招生专业分流实施方案'
            },
            {
              fileName: '近三年推荐免试研究生'
            },
            {
              fileName: '学校实施的奖助学金项目及近三年学生获得各位奖助学金学生名单'
            }
          ]
        },
        {
          fileTitle: '学校招生宣传渠道和平台',
          time: '2015-2016学年  第一学期',
          count: '124次'
        },
        {
          fileTitle: '优质生源基地协议书及反馈表',
          time: '2016-2017学年  第一学期',
          count: '42次'
        },
        {
          fileTitle: '关于印发《优秀新生奖学金管理办法》的通知',
          time: '2016-2017学年  第二学期',
          count: '53次'
        },
        {
          fileTitle: '招生专业分流实施方案',
          time: '2018-2019学年  第二学期',
          count: '25次'
        },
        {
          fileTitle: '近三年推荐免试研究生',
          time: '2016-2017学年  第二学期',
          count: '35次'
        },
        {
          fileTitle: '学校实施的奖助学金项目及近三年学生获得各位奖助学金学生名单',
          time: '2016-2017学年  第一学期',
          count: '64次'
        }
      ]
    }
  },
  methods: {
    view() {
      window.open(process.env.BASE_URL + '/image/2019/12/03/120498589079023.pdf')
    }
  }
}
</script>
<style lang="scss">
.studentFile {
  // padding-top: 30px;
  .el-table__expanded-cell[class*=cell] {
    background-color: #fcfcfc;
  }
  .tableTop {
    overflow: hidden;
    // margin-bottom: 30px;
    .tableTitle {
      // width:140px;
      // height:40px;
      // background:rgba(3,177,135,1);
      // box-shadow:0px 2px 20px 0px rgba(3,177,135,0.3);
      // border-radius:20px;
      // color: #fff;
      // letter-spacing: 0.1em;
      // text-align: center;
      // line-height: 40px;
      float: left;
    }
    .el-select {
      float: right;
      margin-top: 22px;
    }
  }
  .table {
    padding-bottom: 50px;
    background-color: #fff;
    border-radius: 10px;
    margin-bottom: 40px;
  }
}
</style>


